import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { TitleBar } from '../_commons';
import styles from './container.module.css';
import { useSelector } from 'react-redux';
import { selectUser } from '../../store/userSlice';
import Datetime from 'react-datetime';
import 'moment/locale/vi';

// import vi from 'date-fns/locale/vi';
// registerLocale('vi', vi);

function Container() {
  const [categoryList, setCategoryList] = useState([]);
  const [currentCategory, setCurrentCategory] = useState(null);
  const [data, setData] = useState([]);
  const [categoryData, setCategoryData] = useState([]);
  const [roomSelected, setRoomSelected] = useState(null);
  const [loading, setLoading] = useState(false);
  const [inDate, setInDate] = useState(new Date());

  const user = useSelector(selectUser);

  useEffect(() => {
    axios
      .get('/api/guest-house/category', {
        params: { search: { isVisible: true } },
      })
      .then((res) => {
        if (res.status === 200) {
          setCategoryList(res.data.data);
          setCurrentCategory(res.data.data[0]);
        }
      })
      .catch((err) => alert(err.toString()));

    axios
      .get('/api/guest-house/room', { params: { search: { isVisible: true } } })
      .then((res) => {
        if (res.status === 200) {
          setData(res.data.data);
        }
      })
      .catch((err) => alert(err.toString()));
  }, []);

  useEffect(() => {
    const result = data?.filter((child) => {
      return child.category._id === currentCategory._id;
    });

    setCategoryData(result);
  }, [currentCategory, data]);

  const handleCategoryChange = (e) => {
    axios
      .get(`/api/guest-house/category/${e.target.value}`, {
        params: { search: { isVisible: true } },
      })
      .then((res) => {
        if (res.status === 200) {
          setRoomSelected(null);
          setCurrentCategory(res.data.data);
        }
      })
      .catch((err) => alert(err.toString()));
  };

  const handleRoomSelected = (id) => {
    setRoomSelected(id);
  };

  const handleSubmitButton = () => {
    const name = document.getElementById('formName').value;
    const tel = document.getElementById('formTel').value;
    const feedback = document.getElementById('formFeedback').value;

    if (!name || !tel) {
      return alert('Vui lng nhp tn ca bn!');
    }

    if (!roomSelected) {
      return alert('Vui lng chn phng bn mun thu!');
    }

    setLoading(true);

    axios
      .post('/api/guest-house/user', {
        name,
        tel,
        guestHouse: roomSelected,
        feedback,
        inDate,
      })
      .then((res) => {
        setLoading(false);
        return alert(
          'Thng tin ng k ca bn  c ghi nhn, nhn vin nh khch HQG s lin h vi bn  hon tt th tc ng k. Mi thc mc, vui lng lin h 0877.876.877 (Mr. Hun)  c h tr. Xin cm n!'
        );
      })
      .catch((err) => {
        setLoading(false);
        return alert(err.toString());
      });
  };

  const handleDateChange = (e) => {
    setInDate(e._d);
  };

  return (
    <>
      <TitleBar title='t phng nh khch' />
      <div className={styles.container}>
        <p className={styles.title}>Chn loi phng</p>
        <select onChange={handleCategoryChange}>
          {categoryList.map((child) => {
            return (
              <option value={child._id} key={child._id}>
                {child.name}
              </option>
            );
          })}
        </select>
        <p className={styles.categoryDesc}>
          Thng tin loi phng:
          <br />
          {currentCategory?.description}
        </p>
        <p className={styles.title}>Chn phng</p>

        <div
          style={{
            display: 'flex',
            flexWrap: 'wrap',
            justifyContent: 'center',
          }}
        >
          {categoryData.map((child) => {
            return (
              <div
                key={child._id}
                className={styles.roomContainer}
                onClick={() => handleRoomSelected(child._id)}
                style={
                  roomSelected === child._id
                    ? { backgroundColor: 'var(--primary)', color: 'white' }
                    : null
                }
              >
                <span>Phng {child.number}</span>
              </div>
            );
          })}
        </div>

        <div className={styles.formInfo}>
          <div className={styles.formGroup}>
            <label className={styles.formLabel}>Tn ca bn*</label>
            <input
              className={styles.formInput}
              id='formName'
              type='text'
              placeholder='Nhp h tn y , c du'
              defaultValue={user?.data.name}
            />
          </div>
          <div className={styles.formGroup}>
            <label className={styles.formLabel}>S in thoi lin h*</label>
            <input
              className={styles.formInput}
              id='formTel'
              type='text'
              placeholder='Nhp s in thoi ca bn'
              defaultValue={user?.data.tel}
            />
          </div>
          {/* <div className={styles.formGroup}>
            <label className={styles.formLabel}>Ngy vo  d kin*</label>
            <DatePicker
              style={{ width: '100%' }}
              locale='vi'
              selected={inDate}
              onChange={handleDateChange}
            />
          </div> */}
          <div className={styles.formGroup}>
            <label className={styles.formLabel}>
              Thi gian nhn phng (d kin):
            </label>
            <Datetime
              initialValue={inDate}
              locale='vi'
              onChange={handleDateChange}
            />
          </div>
          <div className={styles.formGroup}>
            <label className={styles.formLabel}>Ghi ch/Gp </label>
            <textarea className={styles.formInput} id='formFeedback' />
          </div>
          <div className={styles.formGroup}>
            {loading ? (
              <p className={styles.formSubmitButton}>ang ng k...</p>
            ) : (
              <button
                onClick={handleSubmitButton}
                className={styles.formSubmitButton}
              >
                ng k ngay
              </button>
            )}
          </div>
          <p style={{ margin: '1rem 0' }}>
            Trong qu trnh ng k, nu xy ra li h thng, vui lng chp mn
            hnh li gi v Zalo:{' '}
            <a
              href="href='https://zalo.me/0797324886"
              target='_blank'
              rel='noopener noreferrer'
            >
              0797324886
            </a>{' '}
             c h tr nhanh nht. Xin cm n.
          </p>
        </div>
      </div>
    </>
  );
}

export default Container;
