import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { TitleBar } from '../_commons';
import styles from './container.module.css';

function Container() {
  const [data, setData] = useState([]);
  const [roomSelected, setRoomSelected] = useState(null);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    axios
      .get('/api/guest-house/room')
      .then((res) => {
        setData(res.data.data);

        if (res.data.data[0]) {
          setRoomSelected(res.data.data[0]);
        }
      })
      .catch((err) => {
        alert(err.toString());
      });
  }, []);

  const handleRoomChange = (e) => {
    setRoomSelected(e.target.value);
  };

  const handleSubmitButton = () => {
    const note = document.getElementById('formNote')?.value;

    if (!roomSelected) {
      return alert('Vui lng chn phng ca bn!');
    }

    if (!note) {
      return alert('Vui lng nhp ni dung yu cu sa cha ca bn!');
    }

    setLoading(true);

    axios
      .post('/api/guest-house/report', { guestHouse: roomSelected, note })
      .then((res) => {
        if (res.status === 200) {
          setLoading(false);
          alert('Bn  gi yu cu sa cha thnh cng!');
        }
      })
      .catch((err) => {
        setLoading(false);
        alert(err.toString());
      });
  };

  return (
    <div>
      <TitleBar title='Gi yu cu sa cha' />
      <div className={styles.container}>
        <div className={styles.formGroup}>
          <label className={styles.formLabel}>
            Vui lng chn phng ca bn
          </label>
          <select value={roomSelected} onChange={handleRoomChange}>
            {data.map((child) => {
              return (
                <option key={child._id} value={child._id}>
                  Phng {child.number}
                </option>
              );
            })}
          </select>
        </div>
        <div className={styles.formGroup}>
          <label className={styles.formLabel}>Yu cu</label>
          <textarea
            className={styles.formInput}
            placeholder='Nhp ni dung yu cu sa cha ca bn'
            rows={5}
            id='formNote'
          />
        </div>

        {loading ? (
          <span className={styles.formButton}>ang gi yu cu</span>
        ) : (
          <button className={styles.formButton} onClick={handleSubmitButton}>
            Gi yu cu
          </button>
        )}
      </div>
    </div>
  );
}

export default Container;
