import axios from "axios";
import { ToastWrapper } from "../utils";

export const axiosWrapper = axios.create({
  baseURL: process.env.REACT_APP_BASE_URL || "http://localhost:5000",
  // timeout: 10000,
  headers: {},
});

const handleResponse = (res) => {
  return res.data;
};

const handleError = (err) => {
  console.log(err)
  let errorMsg = "";
  switch (err.response?.status) {
    case 400:
      errorMsg = "Li nhp liu";
      break;
    case 401:
      errorMsg = "Ngi dng cha ng nhp";
      break;
    case 403:
      errorMsg = "Ti nguyn khng c php";
      break;
    case 404:
      errorMsg = "Khng tm thy ti nguyn";
      break;
    case 500:
      errorMsg = "Khng th kt ni n server";
      break;
    default:
      errorMsg = "Li cha xc nh";
      break;
  }

  return ToastWrapper(errorMsg);
};

axiosWrapper.interceptors.request.use(
  (req) => {
    return req;
  },
  (err) => {
    console.log(err);
  }
);

axiosWrapper.interceptors.response.use(handleResponse, handleError);
