import axios from 'axios';
import React, { useEffect, useState } from 'react';
import styles from './registration.module.css';
import Datetime from 'react-datetime';
import 'moment/locale/vi';

function Registration() {
  const [page, setPage] = useState(0);
  const [limit, setLimit] = useState(10);
  const [data, setData] = useState([]);
  const [category, setCategory] = useState([]);
  const [rooms, setRooms] = useState([]);

  useEffect(() => {
    axios
      .get('/api/guest-house/user', {
        params: {
          page,
          limit,
        },
      })
      .then((res) => {
        setData(res.data.data);
      })
      .catch((err) => alert(err.toString()));

    axios
      .get('/api/guest-house/category', {
        params: {
          page: 0,
          limit: 10,
        },
      })
      .then((res) => {
        setCategory(res.data.data);
      })
      .catch((err) => alert(err.toString()));

    axios
      .get('/api/guest-house/room', {
        params: {
          page: 0,
          limit: 100,
        },
      })
      .then((res) => {
        setRooms(res.data.data);
      })
      .catch((err) => alert(err.toString()));
  }, []);

  const updateState = (id, state) => {
    axios
      .patch(`/api/guest-house/user/${id}`, { state })
      .then((res) => {
        if (res.status === 200) {
          axios
            .get('/api/guest-house/user', {
              params: {
                page,
                limit,
              },
            })
            .then((res) => {
              setData(res.data.data);
            })
            .catch((err) => alert(err.toString()));
        }
      })
      .catch((err) => alert(err.toString()));
  };

  const updateInfo = (id) => {
    const name = document.getElementById(`formName_${id}`).value;
    const tel = document.getElementById(`formTel_${id}`).value;
    const feedback = document.getElementById(`formFeedback_${id}`).value;

    axios
      .patch(`/api/guest-house/user/${id}`, { name, tel, feedback })
      .then((res) => {
        if (res.status === 200) {
          return alert(' cp nht thng tin thnh cng!');
        }
      })
      .catch((err) => alert(err.toString()));
  };

  const handleAddRoomButton = (id, roomId) => {
    const confirmed = window.confirm(
      'Bn c chc chn nhp phng khng? D liu ngi dng c s b xa khi phng ny!'
    );

    if (confirmed) {
      const name = document.getElementById(`formName_${id}`).value;
      const tel = document.getElementById(`formTel_${id}`).value;

      axios
        .patch(`/api/guest-house/room/${roomId}`, { name, tel })
        .then((res) => {
          axios
            .patch(`/api/guest-house/user/${id}`, { state: 3 })
            .then((res) => {
              if (res.status === 200) {
                axios
                  .get('/api/guest-house/user', {
                    params: {
                      page,
                      limit,
                    },
                  })
                  .then((res) => {
                    setData(res.data.data);
                    alert(' nhn phng thnh cng!');
                  })
                  .catch((err) => alert(err.toString()));
              }
            })
            .catch((err) => alert(err.toString()));
        })
        .catch((err) => alert(err.toString()));
    }
  };

  const getPrevRoom = () => {
    if (page <= 0) {
      return;
    } else {
      axios
        .get('/api/guest-house/user', {
          params: {
            page: page - 1,
            limit,
          },
        })
        .then((res) => {
          setPage(page - 1);
          setData(res.data.data);
        })
        .catch((err) => alert(err.toString()));
    }
  };

  const getNextRoom = () => {
    axios
      .get('/api/guest-house/user', {
        params: {
          page: page + 1,
          limit,
        },
      })
      .then((res) => {
        setPage(page + 1);
        setData(res.data.data);
      })
      .catch((err) => alert(err.toString()));
  };

  const handleRoomChange = (id, roomId) => {
    axios
      .patch(`/api/guest-house/user/${id}`, { guestHouse: roomId })
      .then((res) => {
        if (res.status === 200) {
          axios
            .get('/api/guest-house/user', {
              params: {
                page,
                limit,
              },
            })
            .then((res) => {
              setData(res.data.data);
            })
            .catch((err) => alert(err.toString()));
        }
      })
      .catch((err) => alert(err.toString()));
  };

  return (
    <>
      <div className={styles.pagingContainer}>
        <button onClick={() => getPrevRoom()}>Trc </button>
        <span>{page + 1}</span>
        <button onClick={() => getNextRoom()}>K tip</button>
      </div>
      {data.length === 0 ? (
        <p style={{ textAlign: 'center', margin: '5rem' }}>Khng c d liu</p>
      ) : null}
      <div className={styles.roomsContainer}>
        {data.map((child) => {
          return (
            <div className={styles.roomContainer} key={child._id}>
              <div className={styles.userInfoContainer}>
                <div>
                  <p>Tn:</p>
                  <textarea
                    defaultValue={child?.name}
                    rows={1}
                    id={`formName_${child._id}`}
                  />
                </div>
                <div>
                  <p>S in thoi:</p>
                  <textarea
                    defaultValue={child?.tel}
                    rows={1}
                    id={`formTel_${child._id}`}
                  />
                </div>
                <div>
                  <p>Ngy vo:</p>
                  {/* <Datetime
                    id={`formInDate_${child._id}`}
                    // initialValue={child.inDate || null}
                    locale='vi'
                    // onChange={handleDateChange}
                  /> */}
                  <p>
                    <span>{new Date(child?.inDate).toLocaleTimeString()}</span>{' '}
                    <span>{new Date(child?.inDate).toLocaleDateString()}</span>
                  </p>
                </div>

                <div>
                  <p>Ghi ch/Gp :</p>
                  <textarea
                    defaultValue={child.feedback ? child.feedback : 'Khng c'}
                    rows={1}
                    id={`formFeedback_${child._id}`}
                  />
                </div>
                <div>
                  <p>Phng:</p>

                  <select
                    value={child.guestHouse._id}
                    onChange={(e) =>
                      handleRoomChange(child._id, e.target.value)
                    }
                  >
                    {rooms.map((child) => {
                      return (
                        <option value={child._id}>Phng {child.number}</option>
                      );
                    })}
                  </select>
                </div>
                <div>
                  <p>Gi ngy: {child.guestHouse?.dailyPrice}</p>
                </div>
                <div>
                  <p>Gi thng: {child.guestHouse?.monthlyPrice}</p>
                </div>
                <div>
                  <p style={{ margin: '1rem 0' }}>
                    <span>Tnh trng: </span>
                    {child.guestHouse?.isAvailable
                      ? 'Cn phng'
                      : 'Khng c sn'}
                  </p>
                </div>
              </div>
              <div className={styles.buttonContainer}>
                <button onClick={() => updateInfo(child._id)}>Cp nht</button>
                <button
                  onClick={() => updateState(child._id, 0)}
                  style={
                    child.state === 0
                      ? { backgroundColor: 'var(--primary)', color: 'white' }
                      : null
                  }
                >
                   to
                </button>
                <button
                  onClick={() => updateState(child._id, 1)}
                  style={
                    child.state === 1
                      ? { backgroundColor: 'var(--primary)', color: 'white' }
                      : null
                  }
                >
                  Ch thanh ton
                </button>
                <button
                  onClick={() => updateState(child._id, 2)}
                  style={
                    child.state === 2
                      ? { backgroundColor: 'var(--primary)', color: 'white' }
                      : null
                  }
                >
                   hon tt
                </button>
                <button
                  style={
                    child.state === 3
                      ? { backgroundColor: 'var(--primary)', color: 'white' }
                      : null
                  }
                  onClick={() =>
                    handleAddRoomButton(child._id, child.guestHouse._id)
                  }
                >
                   nhn phng
                </button>
                <button
                  onClick={() => updateState(child._id, 4)}
                  style={
                    child.state === 4
                      ? {
                          backgroundColor: 'red',
                          color: 'white',
                          borderWidth: '0',
                        }
                      : null
                  }
                >
                   hy
                </button>

                <div></div>
              </div>
            </div>
          );
        })}
      </div>
    </>
  );
}

export default Registration;
