import axios from 'axios';
import React, { useState } from 'react';
import { TitleBar } from '../_commons';
import styles from './styles.module.css';
import busApi from './api';

function Container(props) {
  const [isLoading, setIsLoading] = useState(false);

  const handleSubmitButton = () => {
    setIsLoading(true);
    const formName = document.getElementById('formName').value;
    const formTel = document.getElementById('formTel').value;
    const formZalo = document.getElementById('formZalo').value;
    const formNote = document.getElementById('formNote').value;
    const formAddress = document.getElementById('formAddress').value;

    if (!formName || !formTel) {
      setIsLoading(false);
      return alert(
        'Vui lng nhp y  tn v s in thoi lin h ca bn!'
      );
    }

    busApi
      .createBusUser({
        name: formName,
        tel: formTel,
        zalo: formZalo,
        note: formNote,
        address: formAddress,
      })
      .then((res) => {
        console.log(res);
        if (res.data) {
          setIsLoading(false);
          return alert(
            'ng k thnh cng! Chng mnh s lin h vi bn trong thi gian sm nht. Mi thc mc lin h 0877.876.877  c gii p. Xin cm n!'
          );
        }
      })
      .catch((e) => {
        console.log(e);
        setIsLoading(false);
      });
  };

  return (
    <div>
      <TitleBar title='t xe a rc' navigation='/uniforms' />

      <form className={styles.formContainer}>
        <div className={styles.infoContainer}>
          <p style={{ margin: 0, color: ' #ff0000 ' }}>* bt buc</p>
          <div className={styles.formGroup}>
            <label for='formName' className={styles.formLabel}>
              Tn ca bn*
            </label>
            <input
              className={styles.formInput}
              id='formName'
              type='text'
              placeholder='Nhp y  h tn, c du'
            />
          </div>

          <div className={styles.formGroup}>
            <label for='formTel' className={styles.formLabel}>
              in thoi lin h*
            </label>
            <input
              className={styles.formInput}
              id='formTel'
              type='text'
              placeholder='Nhp s in thoi lin h ca bn'
            />
          </div>
          <div className={styles.formGroup}>
            <label for='formZalo' className={styles.formLabel}>
              S in thoi Zalo (Khng bt buc)
            </label>
            <input
              className={styles.formInput}
              id='formZalo'
              type='text'
              placeholder='Nhp s in thoi zalo ca bn'
            />
          </div>
          <div className={styles.formGroup}>
            <label for='formAddress' className={styles.formLabel}>
              a ch n bn (Khng bt buc)
            </label>
            <input
              className={styles.formInput}
              id='formAddress'
              type='text'
              placeholder='B trng nu cn xc nhn li sau'
            />
          </div>
          <div className={styles.formGroup}>
            <label for='formNote' className={styles.formLabel}>
              Yu cu khc/Ghi ch (nu c)
            </label>
            <textarea
              className={styles.formInput}
              id='formNote'
              rows='5'
              placeholder='Nhp yu cu ca bn...'
            />
          </div>

          {isLoading ? (
            <>
              <p>
                H thng ang x l, vui lng ch trong t nht 15 giy {'<3'}
              </p>
              <p className={styles.formSubmitButton}>ang ng k...</p>
            </>
          ) : (
            <button
              type='button'
              onClick={handleSubmitButton}
              className={styles.formSubmitButton}
            >
              ng k ngay
            </button>
          )}
        </div>
      </form>
    </div>
  );
}

export default Container;
